@import './variables.scss';

#nav-bar {
	display: flex;
	justify-content: space-around;
	background-color: #ffffff09;
	backdrop-filter: blur(5px);
	color: $white;
	height: 3rem;
	width: 100vw;
	position: fixed;
	z-index: 999;
	top: 0;
	font-family: $bebas;
	font-size: larger;
	letter-spacing: 0.25rem;

	.nav-links {
		width: 50%;
		display: flex;
		flex-direction: row;
		align-items: center;
		justify-content: space-around;

		a {
			display: flex;
			align-items: center;
			justify-content: center;
			border-left: 1px solid $white;
			border-right: 1px solid $white;
			height: 1.8rem;
		}

		p {
			cursor: pointer;
			padding: 0 1rem 0 0;
			transition: 0.5s;
			padding-top: 0.25rem;

			&:hover {
				color: $yellow;
			}
		}

		sup {
			color: $yellow;
			padding-left: 1rem;
			margin-bottom: 0.75rem;
		}
	}
}

.btn-flip {
	outline: 0;
	line-height: 40px;
	position: relative;
	text-align: center;
	display: inline-block;
	font-family: $bebas;
	text-transform: uppercase;

	&:hover {
		&:after {
			opacity: 1;
			transform: translateY(0) rotateX(0);
		}

		&:before {
			transform: translateY(50%) rotateX(90deg);
		}
	}

	&:after {
		top: 0;
		left: 0;
		opacity: 0;
		display: block;
		transition: $speed;
		position: absolute;
		padding-top: 0.25rem;
		content: attr(data-back);
		transform: translateY(-50%) rotateX(90deg);
	}

	&:before {
		top: 0;
		left: 0;
		opacity: 1;
		display: block;
		line-height: 40px;
		transition: $speed;
		position: relative;
		content: attr(data-front);
		transform: translateY(0) rotateX(0);
	}
}

